import { defineStore } from 'pinia'

const SERVER = 'http://localhost:3000';

export const useAuthStore = defineStore('auth', {
  state: () => ({
    user: null,   // { id, email, role }
    token: localStorage.getItem('token') || null,
  }),

  getters: {
    role: (state) => state.user?.role || null,
    isAuthenticated: (state) => !!state.token
  },

  actions: {
    async login({ email, password }) {
      // —à–∞–≥ 1: –ª–æ–≥–∏–Ω–∏–º—Å—è –∏ –ø–æ–ª—É—á–∞–µ–º —Ç–æ–∫–µ–Ω
      const res = await fetch(`${SERVER}/api/auth/login`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password })
      })

      if (!res.ok) throw new Error('–ù–µ–≤–µ—Ä–Ω—ã–π –ª–æ–≥–∏–Ω –∏–ª–∏ –ø–∞—Ä–æ–ª—å')

      const { data } = await res.json()
      this.token = data.token
      localStorage.setItem('token', this.token)

      // —à–∞–≥ 2: —Å—Ä–∞–∑—É –ø–æ–¥—Ç—è–≥–∏–≤–∞–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
      await this.fetchMe()
    },

    async register(dto) {
      const res = await fetch(`${SERVER}/api/auth/register`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(dto)
      })

      if (!res.ok) throw new Error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏')

      console.log(await res.json())

      return (await res.json()).data.userId
    },

    async fetchMe() {
      if (!this.token) return

      const res = await fetch(`${SERVER}/api/users/me`, {
        headers: { Authorization: `Bearer ${this.token}` }
      })

      if (!res.ok) {
        this.logout()
        throw new Error('–ù–µ —É–¥–∞–ª–æ—Å—å –∑–∞–≥—Ä—É–∑–∏—Ç—å –ø—Ä–æ—Ñ–∏–ª—å')
      }

      const { data } = await res.json()
      this.user = data
    },

    logout() {
      this.user = null
      this.token = null
      localStorage.removeItem('token')
    }
  }
})
