<template>
  <v-container>
    <v-row justify="center">
        <v-card-title>
            <v-btn color="primary" @click="getTasks" class="mr-2">–û–±–Ω–æ–≤–∏—Ç—å</v-btn>
            <v-btn color="success" @click="showAddDialog = true">–î–æ–±–∞–≤–∏—Ç—å –∑–∞–¥–∞—á—É</v-btn>
        </v-card-title>
    </v-row>

    <!-- –¥–∏–∞–ª–æ–≥ –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –∑–∞–¥–∞—á–∏ -->
    <v-dialog v-model="showAddDialog" max-width="500px">
      <v-card>
        <v-card-title>–î–æ–±–∞–≤–∏—Ç—å –∑–∞–¥–∞—á—É</v-card-title>
        <v-card-text>
          <v-text-field v-model="newTask.title" label="–ù–∞–∑–≤–∞–Ω–∏–µ" required />
          <v-text-field v-model="newTask.description" label="–û–ø–∏—Å–∞–Ω–∏–µ" required />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="closeAddDialog">–û—Ç–º–µ–Ω–∞</v-btn>
          <v-btn color="success" @click="addTask" :loading="isAddingTask">
            –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>

</template>

<script setup>
import { ref, defineProps } from 'vue'
import { taskApi } from '../../api/task.api.js'


const newTask = ref({
  title: '',
  description: ''
})
const props = defineProps({
  getTasks: Function
})

const getTasks = props.getTasks;

const showAddDialog = ref(false)
const isAddingTask = ref(false)

async function addTask() {
  if (!newTask.value.title || !newTask.value.description) return

  try {
    isAddingTask.value = true
    const response = await taskApi.add(newTask.value);
    if (response.ok) {
      closeAddDialog()
      await getTasks();
    }
  } finally {
    isAddingTask.value = false
  }
}

function closeAddDialog() {
  showAddDialog.value = false
  newTask.value = { title: '', description: '' }
}

</script>