<template>
  <v-container>
    <v-row justify="center">
      <v-col cols="12" md="8">
        <v-card>
          <v-data-table
            :headers="headers"
            :items="users"
            item-key="id"
            class="elevation-1"
          >
            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.title="{ item }">
                {{ item.name }}
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.description="{ item }">
                {{ item.description }}
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.status="{ item }">
              <v-chip
                :color="item?.status === 'completed' ? 'green' : 'orange'"
                variant="flat"
              >
                {{ item?.status }}
              </v-chip>
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.actions="{ item }">
              <v-btn
                color="red"
                size="small"
                @click="deleteTask(item.id)"
                :loading="deletingUsers.includes(item.id)"
              >
                –£–¥–∞–ª–∏—Ç—å
              </v-btn>
            </template>

          </v-data-table>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script setup>
import { ref, defineProps, toRef } from 'vue'
import { userApi } from '../../api/user.api.js'

// –ø–æ–ª—É—á–∞–µ–º –ø—Ä–æ–ø
const props = defineProps({
  users: Array,
  getUsers: Function
})

const users = toRef(props, 'users') // —Ç–µ–ø–µ—Ä—å –∏—Å–ø–æ–ª—å–∑—É–µ–º –ø—Ä–æ–ø—Å –Ω–∞–ø—Ä—è–º—É—é
const getUsers = props.getUsers;

const deletingUsers = ref([])

// –ò—Å–ø—Ä–∞–≤–ª–µ–Ω–Ω—ã–µ –∑–∞–≥–æ–ª–æ–≤–∫–∏ –¥–ª—è Vuetify 3
const headers = [
  { title: '–ò–º—è', key: 'name' },
  { title: '–ü–æ—á—Ç–∞', key: 'email' },
  { title: '–†–æ–ª—å', key: 'role' },
  { title: '–ê–∫—Ç–∏–≤–Ω—ã–π', key: 'isActive' },
  { title: '–î–µ–π—Å—Ç–≤–∏—è', key: 'actions', sortable: false },
]
  
async function deleteTask(userId) {
  try {
    deletingUsers.value.push(userId)
    const response = await userApi.delete(userId);
    if (response.ok) {
      await getUsers()
      users.value = users.value.filter(t => t.id !== userId)
    }
  } finally {
    deletingUsers.value = deletingUsers.value.filter(id => id !== userId)
  }
}
</script>