<template>
  <v-container>
    <v-row justify="center">
      <v-col cols="12" md="10">
        <v-card>
          <v-data-table
            :headers="headers"
            :items="tasks"
            item-key="id"
            class="elevation-1"
          >
            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.title="{ item }">
                {{ item.title }}
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.description="{ item }">
                {{ item.description }}
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.assignments.worker="{ item }">
              <v-menu>
                <template #activator="{ props }">
                  <v-btn v-bind="props" color="green" size="small">
                    {{ (item.assignments.length === 0) ? '–ù–µ –Ω–∞–∑–Ω–∞—á–µ–Ω' : item.assignments[0]?.user?.name + '...' }}
                  </v-btn>
                </template>

                <v-list>
                  <v-list-item
                    v-for="(assignment, i) in item.assignments"
                    :key="i"
                  >
                    <v-list-item-title class="text-body-2">
                      {{ assignment.user?.name || '–ë–µ–∑ –∏–º–µ–Ω–∏' }}
                    </v-list-item-title>
                  </v-list-item>
                  <v-list-item v-if="!item.assignments || item.assignments.length === 0">
                    <v-list-item-title class="text-grey">–ù–∏–∫—Ç–æ –Ω–µ –Ω–∞–∑–Ω–∞—á–µ–Ω</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.status="{ item }">
              <v-chip
                :color="getStatusColor(item?.status)"
                variant="flat"
              >
                {{ getStatusText(item?.status) }}
              </v-chip>
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.actions="{ item }">
              <div class="d-flex ga-2">
                <v-btn 
                  color="primary" 
                  size="small" 
                  @click="openEditDialog(item)"
                >
                  –û–±–Ω–æ–≤–∏—Ç—å
                </v-btn>
                <v-btn
                  color="red"
                  size="small"
                  @click="deleteTask(item.id)"
                  :loading="deletingTasks.includes(item.id)"
                >
                  –£–¥–∞–ª–∏—Ç—å
                </v-btn>
              </div>
            </template>

          </v-data-table>
        </v-card>
      </v-col>
    </v-row>

    <!-- –î–∏–∞–ª–æ–≥ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∑–∞–¥–∞—á–∏ -->
    <v-dialog v-model="showEditDialog" max-width="600px">
      <v-card v-if="editingTask">
        <v-card-title>–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –∑–∞–¥–∞—á—É</v-card-title>
        <v-card-text>
          <v-text-field 
            v-model="editingTask.title" 
            label="–ù–∞–∑–≤–∞–Ω–∏–µ" 
            required 
            class="mb-3"
          />
          
          <v-textarea 
            v-model="editingTask.description" 
            label="–û–ø–∏—Å–∞–Ω–∏–µ" 
            required 
            rows="3"
            class="mb-4"
          />

          <v-select
            v-model="editingTask.status"
            :items="statusOptions"
            item-title="label"
            item-value="value"
            label="–°—Ç–∞—Ç—É—Å –∑–∞–¥–∞—á–∏"
            class="mb-4"
          />


          <!-- –í—ã–±–æ—Ä –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π -->
          <v-select
            v-model="editingTask.workerIds"
            :items="users"
            item-title="name"
            item-value="id"
            label="–ù–∞–∑–Ω–∞—á–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π"
            multiple
            chips
            closable-chips
            :loading="loadingUsers"
            class="mb-3"
          >
            <template #selection="{ item, index }">
              <v-chip
                v-if="index < 3"
                size="small"
                closable
                color="primary"
                variant="tonal"
                @click:close="removeUser(item.raw.id)"
              >
                <v-icon start>mdi-account</v-icon>
                {{ item.title }}
              </v-chip>
              <span
                v-if="index === 3"
                class="text-grey text-caption align-self-center ml-2"
              >
                (+{{ editingTask.workerIds.length - 3 }} –¥—Ä—É–≥–∏—Ö)
              </span>
            </template>

            <template #item="{ props, item }">
              <v-list-item v-bind="props" :title="item.title">
                <template #prepend>
                  <v-avatar size="32" :color="getRoleColor(item.raw.role)">
                    <v-icon>mdi-account</v-icon>
                  </v-avatar>
                </template>
                <template #append>
                  <v-chip size="x-small" :color="getRoleColor(item.raw.role)">
                    {{ item.raw.role }}
                  </v-chip>
                  <v-icon 
                    v-if="editingTask.workerIds.includes(item.value)" 
                    color="success"
                    class="ml-2"
                  >
                    mdi-check
                  </v-icon>
                </template>
              </v-list-item>
            </template>

            <template #no-data>
              <v-list-item>
                <v-list-item-title>–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω—ã</v-list-item-title>
              </v-list-item>
            </template>
          </v-select>

          <!-- –ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ —Ç–µ–∫—É—â–∏—Ö –Ω–∞–∑–Ω–∞—á–µ–Ω–∏—è—Ö -->
          <v-alert 
            v-if="editingTask.workerIds.length === 0" 
            type="warning" 
            variant="tonal"
            class="mb-3"
          >
            –ó–∞–¥–∞—á–∞ –Ω–µ –Ω–∞–∑–Ω–∞—á–µ–Ω–∞ –Ω–∏ –æ–¥–Ω–æ–º—É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é
          </v-alert>

        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn variant="text" @click="closeEditDialog">
            –û—Ç–º–µ–Ω–∞
          </v-btn>
          <v-btn 
            color="success" 
            @click="saveEditTask" 
            :loading="isAddingTask"
            :disabled="!editingTask.title || !editingTask.description"
          >
            –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </v-container>
</template>

<script setup>
import { ref, defineProps, toRef, onMounted } from 'vue'
import { taskApi } from '../../api/task.api.js'
import { userApi } from '../../api/user.api.js'

const statusOptions = [
  { label: '–û–∂–∏–¥–∞–µ—Ç', value: 'pending' },
  { label: '–í –ø—Ä–æ—Ü–µ—Å—Å–µ', value: 'in_progress' },
  { label: '–ó–∞–≤–µ—Ä—à–µ–Ω–æ', value: 'completed' }
]

  // –ø–æ–ª—É—á–∞–µ–º –ø—Ä–æ–ø
const props = defineProps({
  tasks: Array,
  getTasks: Function
})

const tasks = toRef(props, 'tasks')
const getTasks = props.getTasks

const users = ref([])
const deletingTasks = ref([])
const showEditDialog = ref(false)
const isAddingTask = ref(false)
const editingTask = ref(null)
const loadingUsers = ref(false)

// –ó–∞–≥—Ä—É–∑–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π
async function getUsers() {
  try {
    loadingUsers.value = true
    const response = await userApi.getAll()

    const data = await response.json()
    console.log('Users loaded:', data.data.users)
    users.value = data.data.users
    
  } catch (error) {
    console.error('Error fetching users:', error)
  } finally {
    loadingUsers.value = false
  }
}

onMounted(getUsers)

// –ó–∞–≥–æ–ª–æ–≤–∫–∏ —Ç–∞–±–ª–∏—Ü—ã
const headers = [
  { title: '–ù–∞–∑–≤–∞–Ω–∏–µ', key: 'title' },
  { title: '–û–ø–∏—Å–∞–Ω–∏–µ', key: 'description' },
  { title: '–°—Ç–∞—Ç—É—Å', key: 'status' },
  { title: '–û—Ç', key: 'createdBy.name' },
  { title: '–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏', key: 'assignments.worker' },
  { title: '–î–µ–π—Å—Ç–≤–∏—è', key: 'actions', sortable: false },
]

// –û—Ç–∫—Ä—ã—Ç–∏–µ –¥–∏–∞–ª–æ–≥–∞ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è
function openEditDialog(task) {
  console.log('Opening edit dialog for task:', task)
  console.log('Task assignments:', task.assignments)

  editingTask.value = {
    ...task,
    // –ò–°–ü–†–ê–í–õ–ï–ù–ò–ï: –ø—Ä–∞–≤–∏–ª—å–Ω–æ –∏–∑–≤–ª–µ–∫–∞–µ–º ID –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π –∏–∑ –Ω–∞–∑–Ω–∞—á–µ–Ω–∏–π
    workerIds: task.assignments?.map(assignment => assignment.userId) || []
  }
  
  console.log('Selected worker IDs:', editingTask.value.workerIds)
  showEditDialog.value = true
}

// –ó–∞–∫—Ä—ã—Ç–∏–µ –¥–∏–∞–ª–æ–≥–∞
function closeEditDialog() {
  showEditDialog.value = false
  editingTask.value = null
}

// –£–¥–∞–ª–µ–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –∏–∑ –≤—ã–±—Ä–∞–Ω–Ω—ã—Ö
function removeUser(userId) {
  editingTask.value.workerIds = editingTask.value.workerIds.filter(id => id !== userId)
}

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è –Ω–∞–∑–Ω–∞—á–µ–Ω–∏–π —á–µ—Ä–µ–∑ –ø—Ä–∞–≤–∏–ª—å–Ω—ã–π API
async function removeTaskAssignments(taskId, currentAssignments) {
  try {
    // –ò—Å–ø–æ–ª—å–∑—É–µ–º –ø—Ä–∞–≤–∏–ª—å–Ω—ã–π API –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è –Ω–∞–∑–Ω–∞—á–µ–Ω–∏–π
    for (const assignment of currentAssignments) {
      const response = await taskApi.removeAssignment(assignment.id)
      
      if (!response.ok) {
        console.error(`–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ –Ω–∞–∑–Ω–∞—á–µ–Ω–∏—è ${assignment.id}:`, await response.text())
      }
    }
  } catch (error) {
    console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ –Ω–∞–∑–Ω–∞—á–µ–Ω–∏–π:', error)
    throw error
  }
}

// –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –∑–∞–¥–∞—á–∏
async function saveEditTask() {
  if (!editingTask.value.title || !editingTask.value.description ||  !editingTask.value.status) return

  try {
    isAddingTask.value = true
    
    // 1. –û–±–Ω–æ–≤–ª—è–µ–º –æ—Å–Ω–æ–≤–Ω—É—é –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –æ –∑–∞–¥–∞—á–µ
    const taskResponse = await taskApi.edit(editingTask.value.id, {
      title: editingTask.value.title,
      description: editingTask.value.description,
      status: editingTask.value.status
    })

    if (!taskResponse.ok) {
      throw new Error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–∏ –∑–∞–¥–∞—á–∏')
    }

    // 2. –£–¥–∞–ª—è–µ–º –≤—Å–µ —Å—Ç–∞—Ä—ã–µ –Ω–∞–∑–Ω–∞—á–µ–Ω–∏—è —á–µ—Ä–µ–∑ –ø—Ä–∞–≤–∏–ª—å–Ω—ã–π API
    if (editingTask.value.assignments && editingTask.value.assignments.length > 0) {
      await removeTaskAssignments(editingTask.value.id, editingTask.value.assignments)
    }

    // 3. –î–æ–±–∞–≤–ª—è–µ–º –Ω–æ–≤—ã–µ –Ω–∞–∑–Ω–∞—á–µ–Ω–∏—è
    for (const userId of editingTask.value.workerIds) {
      const assignResponse = await taskApi.assignUser({
          taskId: editingTask.value.id,
          userId: userId,
      })
      
      if (!assignResponse.ok) {
        console.error(`–û—à–∏–±–∫–∞ –Ω–∞–∑–Ω–∞—á–µ–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è ${userId}:`, await assignResponse.text())
      }
    }

    // 4. –û–±–Ω–æ–≤–ª—è–µ–º —Å–ø–∏—Å–æ–∫ –∑–∞–¥–∞—á –∏ –∑–∞–∫—Ä—ã–≤–∞–µ–º –¥–∏–∞–ª–æ–≥
    await getTasks()
    closeEditDialog()
    
  } catch (error) {
    console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–∏:', error)
  } finally {
    isAddingTask.value = false
  }
}

// –£–¥–∞–ª–µ–Ω–∏–µ –∑–∞–¥–∞—á–∏
async function deleteTask(taskId) {
  try {
    deletingTasks.value.push(taskId)
    await taskApi.delete(taskId)
    await getTasks()
    tasks.value = tasks.value.filter(t => t.id !== taskId)
  } catch (error) {
    console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏:', error)
  } finally {
    deletingTasks.value = deletingTasks.value.filter(id => id !== taskId)
  }
}

// –£—Ç–∏–ª–∏—Ç—ã –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è
function getRoleColor(role) {
  switch (role) {
    case 'admin': return 'red'
    case 'manager': return 'blue'
    case 'worker': return 'green'
    default: return 'grey'
  }
}

function getStatusColor(status) {
  switch (status) {
    case 'completed': return 'green'
    case 'in_progress': return 'blue'
    case 'pending': return 'orange'
    default: return 'grey'
  }
}

function getStatusText(status) {
  switch (status) {
    case 'completed': return '–ó–∞–≤–µ—Ä—à–µ–Ω–æ'
    case 'in_progress': return '–í –ø—Ä–æ—Ü–µ—Å—Å–µ'
    case 'pending': return '–û–∂–∏–¥–∞–µ—Ç'
    default: return status
  }
}
</script>